@import "../../../globalStyles/commons";


.breadcrumb {
  display: flex;
  align-items: center;
}

slot::slotted(*) {
  &::before {
    display: inline-block;
    padding-right: 0.5rem;
    padding-left: 0.5rem;
    color: var(--border-color);
    content: "/";
  }
}

slot::slotted(*:first-child) {
  &::before {
    display: none;
  }
}

